
export default function ValidationCalculateCpm(pal, ppm ) {
    const ppmValue = parseInt(ppm);
    if (pal === false) {
        return "Wybierz warto wspczynnika aktywnoci fizycznej PAL"
    }
    if (Number.isNaN( ppmValue ) || ppmValue < 2) {
        return "Podaj poprawn warto podstawowej przemiany materii"
    }
    return 0;
}